/**DETAIL QUIZ**/
.item_result_quiz{
    p{text-align: center; margin-bottom: 5px;
        &.text-point{font-family: $font-title; font-size: 72px; line-height: 91px; font-weight: 700; position: relative; padding-bottom: 20px; margin-bottom: 20px;
            &:after{
                content: '';
                width: calc(100% - 80px);
                height: 1px;
                background: var(--border);
                position: absolute;
                left: 40px;
                bottom: 0;
            }
        }
    }
    .text-point + p{margin-bottom: 20px;}
    .btn_restart{width: 100%;background: #F53520;
        .icon-svg{fill: #fff;}
    }
    .box-result-percent{
        .item-result-per{
            margin-bottom: 20px;
            .text-ans{width: 70px;}
            .percent-bar{
                width: calc(100% - 180px);
                font-size: 14px;
                color: #F53520;
                text-align: right;
                padding-left: 55px;
                position: relative;   
                margin-top: 0;
                .number-per {
                    width: 40px;
                    position: absolute;
                    top: 0;
                    left: 0
                }
                
                .main-bar-per {
                    width: 100%;
                    float: left;
                    height: 4px;
                    background: #E5E5E5;
                    border-radius: 3px;
                    position: relative;
                    margin-top: 13px
                }
                
                .main-bar-per .real-bar-per {
                    position: absolute;
                    top: 0;
                    left: 0;
                    height: 100%;
                    background: #F53520;
                    border-radius: 3px
                }                             
            }
            .count-person {
                width: 110px;
                text-align: right;
                font-size: 14px;
                color: #757575;
                .number-person {
                    font-size: 18px;
                    color: #222
                }
            }
        }
    }
}

.box-share-res{padding-top: 30px;
    p{font-size: 15px; margin-bottom: 10px;}
    .social-com{justify-content: center;
        .circle_s{margin: 0 5px;
            &.mail,&.share-link{
                &:hover{
                    .icon-svg{fill: #087CCE;}
                }
            }
        }
    }
}

/**GALLERY ON DETAIL**/
.img_holder{position: relative;
    .img_bg{
        position: relative;
        width: 100%;
        height: 0px;
        padding-bottom: 60%;
        background-color: #F0EEEA;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        img{max-width: 100%; max-height: 100%; position: absolute; top: 50%; left: 50%;transform: translate(-50%, -50%);}
    }
}


.wrapper_slide_thumb{margin-top: 5px; position: relative;
    .swiper-slide{cursor: pointer; margin-bottom: 5px;
        &:before{
            width: 100%; height: 100%; background: rgba(0,0,0,0.7); position: absolute; top: 0; left: 0; content: ''; z-index: 1; opacity: 0; @extend %has_transition;
        }
        &:after{
            width: 100%; height: 3px; background: #B52759; position: absolute; bottom: -5px; left: 0; content: ''; z-index: 1; opacity: 0; @extend %has_transition;
        }
        &.swiper-slide-thumb-active{
            &:before, &:after{opacity: 1;}
        }
    }
    .swiper-button-slide{
       outline: none; cursor: pointer; width: 25px;height: 50px; position: absolute; top: 47%; transform: translateY(-50%); line-height: 50px; text-align: center; background: rgba(34,34,34,0.75);z-index: 1;
        .icon-svg{width: 16px;height: 23px;fill: #fff;}
        &.swiper-button-next-slide{right: 0;}
        &.swiper-button-prev-slide{left: 0;}
    }
}

.btn_guicauhoi_detail{
    background: #F0EEEA;
    border-radius: 3px;
    font-size: 15px; line-height: 17px; color: var(--gray1);
    padding: 12px;
    text-align: center;
    margin-bottom: 24px;
    a{color: var(--blue); text-decoration: underline; text-underline-position: under;}
    .icon-svg{fill: #bdbdbd; vertical-align: top; margin-right: 10px;}
}

.pin-comment{position: -webkit-sticky !important;position: sticky !important;height: 100%;top: 20px;}
